<template>
	<view>
		<hx-navbar ref="hxnb" :config="config" @clickBtn="onClickBtn" @searchConfirm="searchConfirm" @searchClick="searchClick"/>
		

		<image src="/static/xj.jpg" mode="" style="width: 100%;"></image>
		<view style="background: #FFFFFF;padding:0 15px 15px;" v-for="(item,index) in 4" :key="index">
			
			<text>{{item}}.{{doc}}</text>
			<view style="padding-top: 15px;"/>
			<text>{{doc2}}</text>
		</view>
	</view>
</template>

<script>

	export default {
		
		data() {
			return {
				config:{
					back: false,
					title: '这里是新疆',
					color: '#ffffff',
					// #ifdef APP-PLUS
					backgroundColor: [1,['#a9a1ff','#6970ff','#ff55ff','#ff9999']],
					// #endif
					// #ifndef APP-PLUS
					backgroundColor: [1,['#6970ff','#ff55ff']],
					// #endif
					search: {
						value:'',
						placeholder: '',
						disabled: false
					},
					leftButton:[{
						key: 'btn1',
						icon: '&#xe651;',
						txt: '点我',
						position: 'left'
					}],
				},
				index: 0,
				"doc": "没到过新疆的人，闭上眼睛想想跳入脑海中的新疆元素肯定是草原、骆驼、马、羊和扎着许多小辫的维吾尔族姑娘。",
				"doc2": "其实现在新疆的城市和内地没任何区别，千篇一律，千人一面，乏善可陈。但是，新疆和内地确实不同，城市之外有荒无人烟的辽阔戈壁，浩瀚如海的沙漠，碧波荡漾的草原，还有宝石般的高山湖泊，巍峨的天山昆仑和阿勒泰山及纵横交错的河流及广阔田野。"
			}
		},
		methods: {
			// 当输入完点击完成时触发
			searchConfirm(data){
				//console.log(data.value);
				uni.showToast({
				    title: `输入了${data.value}`,
					icon:'none',
				    duration: 1300
				});
			},
			// 当点击了搜索框时触发
			searchClick(data){
				uni.showToast({
				    title: `点击了搜索框`,
					icon:'none',
				    duration: 1300
				});
			},
			// 按钮点击事件
			onClickBtn(data){
				this.index ++;
			
				if(this.index%2 != 0){
					// 动态修改按钮
					this.$refs.hxnb.conf.leftButton = [{
						key: 'btn1',
						icon: '&#xe650;',
						txt: '库车',
						color: '#ffeb3b',
						position: 'left'
					}]
				}else{
					this.$refs.hxnb.conf.leftButton = [{
						key: 'btn1',
						icon: '&#xe651;',
						txt: '阿克苏',
						position: 'left'
					}]
				}
				
				//console.log(data);
				uni.showToast({
				    title: `3秒后返回主页`,
					icon:'none',
				    duration: 3000
				});
				setTimeout(()=>{
					uni.navigateBack();
				},3000)
				
			},
		}
	}
</script>

<style>

</style>
